body {
  margin: 0;
  padding: 0;
  font-family: 'Montserrat', sans-serif;
  font-size: 14px;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f3f3ec;
}

ul {
  list-style: none;
  list-style-position: inside;
  padding-left: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  /* background-image: url("https://www.juliusbaer.com/fileadmin/_processed_/0/b/csm_JB_IMG_Places_353_BGL1_RGB_LO_721ab00129.jpg"); */
  /* background-size: cover; */
}

.App-header {
  background-color: white;
  color: #141e55;

  overflow: hidden;
  position: fixed;
  top: 0;
  width: 100%;

  padding-bottom: 10px;
  box-shadow: 0px 1px 4px rgba(0, 0, 0, .5);

  float: left;

  text-transform: uppercase;
}

.Logo {
  height: 30px;
  margin-top: 10px;
}

.App-header-text {
  float:left;
  width: 100%;
  color: #001489;
}

.App-header-help {
  margin: 19px;
  position: absolute; 
}

.App-header h1 {
margin: 2px;
}

.App-header p {
  margin: 0px;
}  

.App-content {
  margin-top: 85px;
}

footer {
  color: gray;
  font-size: small;
  margin-bottom: 10px;
}
.AppList {
    text-align: center;
}


li.AppStore {
    background-color: white;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    box-shadow: 0px 30px 20px -25px #bbb;
    padding: 10px;
    margin-bottom: 15px;
}

.AppStore img {
    width: 100px;
    height: 100px;
    border-radius: 18%;
}

.AppName {
    color: #141e55;
    font-size: x-large;
}

.AppStore p {
    margin-top: 0px;
    margin-bottom: 10px;
}
.AppItems {
    padding: 10px;
    font-size: medium;
}

/* unvisited link */
a:link {
    color: #141e55;
  }
  
  /* visited link */
  a:visited {
    color: #141e55;
  }
  
  /* mouse over link */
  a:hover {
    color: #b0aa7e;
  }
  
  /* selected link */
  a:active {
    color: #b0aa7e;
  }
.AppItem {
    padding: 5px;
}

.AppItem a {
    font-size: large;
}
.Close-button {
    /* float: right;
    margin-left: 19px;
    margin-top: -10px; */


    /* overflow: hidden; */
    position: fixed;
    right: 19px;
}

.Help-content {
    clear: both;
    width: 90%;
    margin: auto;
    margin-bottom: 10px;
    font-size: medium;
}

.Help-content p {
    text-align: left;
}

.Help-content img {
    width: 70%;
    box-shadow: 0px 30px 20px -25px #222;
}

